<template>
  <div
    :class="[
      'ac-esign-tool-wrapper',
      {
        has_border,
        has_focus: value,
        hide_events: !has_wrapper,
        editable: editable || value,
      },
    ]"
    v-bind="wrapper_attributes"
    @focus="start_edit"
    @blur="on_blur"
    @keyup.enter="on_enter"
    @keyup.esc="stop_edit"
    @keyup.delete="delete_tool"
  >
    <div v-if="value && has_wrapper" class="ac-esign-tool-wrapper_edit">
      <div>
        <!-- <template v-if="is_admin">
          <span :class="['fe fe-edit-3 text-muted mt-2', { active: editable }]" :id="`tooltip-edit_${_uid}`" @mousedown="set_editable"></span>
          <b-tooltip :target="`tooltip-edit_${_uid}`" triggers="hover">
            {{ editable ? "Edit mode" : `View mode` }}
          </b-tooltip>
        </template> -->
        <template v-if="is_admin && editable">
          <span class="mr-4 mt-2 pt-1"></span>
          <span :class="['fe fe-copy text-muted']" :id="`tooltip-copy_${_uid}`" @mousedown.prevent.stop="copy_tool"></span>
          <b-tooltip :target="`tooltip-copy_${_uid}`" triggers="hover">
            {{ `Copy` }}
          </b-tooltip>
        </template>
        <template v-if="is_admin && editable">
          <span class="mr-4 mt-2 pt-1"></span>
          <span :class="['fe fe-book text-muted']" :id="`tooltip-remove_${_uid}`" @mousedown.prevent.stop="remove"></span>
          <b-tooltip :target="`tooltip-remove_${_uid}`" triggers="hover">
            {{ `Erase` }}
          </b-tooltip>
        </template>
      </div>
    </div>
    <vue-resizable v-if="resize && has_wrapper" v-bind="get_init_resize_props" :class="{ hide_handlers: !value }" @resize:end="on_resize_end">
      <slot />
    </vue-resizable>
    <slot v-else />
  </div>
</template>
<script>
  import VueResizable from "vue-resizable";
  import { remove_element } from "../utils";

  /**
   * Ac Esign Tool Wrapper - wrapper of tool component
   *
   * @requires VueResizable
   *
   * @event enter
   * @event delete
   *
   * @event input
   * @param {boolean}
   *
   * @event update:editable
   * @param {boolean}
   *
   * @event update:selected_user
   * @param {number}
   */
  export default {
    name: "AcEsignToolWrapper",

    components: {
      VueResizable,
    },

    props: {
      /**
       * Edit state
       */
      value: {
        type: Boolean,
        default: false,
      },

      /**
       * Property for resize mode
       */
      resize: {
        type: Boolean,
        default: false,
      },

      /**
       * Resize properties for vue-resizable
       */
      resize_props: {
        type: Object,
      },

      has_border: {
        type: Boolean,
        default: true,
      },

      editable: {
        type: Boolean,
        default: true,
      },

      /**
       * List of users
       */
      users_list: {
        type: Array,
      },

      /**
       * User can be edit current tool
       */
      selected_user: [Number, String],

      is_admin: {
        type: Boolean,
        default: false,
      },

      has_wrapper: {
        type: Boolean,
        default: true,
      },
    },

    computed: {
      /**
       * Get width and height from slot for vue-resizable component
       * @returns {{width: Number, height: Number}}
       */
      get_init_resize_props() {
        const { width, height } = this.resize_props;

        return {
          width,
          height,
        };
      },
      /**
       * Options for user select
       */
      users() {
        return [
          { value: null, text: "all" },
          ...this.users_list.map((u) => ({
            value: u.id,
            text: `${u.name}. ID = ${u.id}`,
          })),
        ];
      },

      selected_user_name() {
        return (
          this.users_list.find((u) => u.id === this.selected_user) || {
            name: "all",
          }
        ).name;
      },

      wrapper_attributes() {
        const attributes = {};

        if (this.has_wrapper) {
          attributes.tabindex = 0;
        }

        return attributes;
      },
    },

    data() {
      return {
        popover: false,
      };
    },

    watch: {
      popover(old_val, new_val) {
        if (new_val && this.$refs.user_select) {
          this.$refs.user_select.focus();
        }
      },
    },

    methods: {
      copy_tool() {
        this.$emit("copy_tool");
      },
      /**
       * Toggle tool editability
       */
      set_editable() {
        this.$emit("update:editable", !this.editable);
      },

      /**
       * Select user for current tool
       */
      on_input_user(user_id) {
        this.$emit("update:selected_user", user_id);
      },

      /**
       * Triggered by on keyup enter key
       */
      on_enter(event) {
        if (!event.shiftKey) {
          this.$emit("enter");
        }
      },

      /**
       * Emit true value
       */
      start_edit() {
        if (!this.editable && !this.is_admin) return;
        this.$emit("input", true);
      },

      /**
       * Emit false value
       */
      stop_edit() {
        this.$emit("input", false);
      },

      /**
       * Triggered on blur wrapper container
       */
      on_blur(ev) {
        let value = ev.currentTarget.contains(ev.relatedTarget) || (ev.relatedTarget && ev.relatedTarget.classList.contains("custom-select"));
        value = value || false;
        this.$emit("input", value);
      },

      remove() {
        this.$emit("remove");
      },

      /**
       * Remove current element from parent
       * @param {Event} ev
       */
      delete_tool(ev) {
        if (ev.code === "Backspace") return;
        this.$emit("delete");
        this.$slots.default.forEach((slot) => {
          if (slot.context) {
            slot.context.$destroy();
          }
        });
        remove_element(this.$el);
      },

      on_resize_end(data) {
        this.$emit("resize_end", data);
      },
    },
  };
</script>

<style>
  .ac-esign-tool-wrapper {
    outline: none;
    /* opacity: 0.6; */
  }

  .ac-esign-tool-wrapper.has_focus {
    z-index: 2;
  }

  .ac-esign-tool-wrapper.editable {
    opacity: 1;
  }

  .ac-esign-tool-wrapper_user {
    position: absolute;
    top: -100%;
    transform: translateY(100%);
    left: 0;
  }
  .ac-esign-tool-wrapper.has_border .ac-esign-tool-wrapper_edit {
    background: none;
  }
  .ac-esign-tool-wrapper_edit {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: calc(100% + 20px);
    height: calc(100% + 20px);
    transform: translate(-10px, -10px);
    background: transparent;
    z-index: -1;
    border: 2px solid;
  }

  .ac-esign-tool-wrapper_edit > div {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(calc(100% + 8px), -50%);
    cursor: pointer;
    display: flex;
    flex-direction: column;
  }

  .ac-esign-tool-wrapper_edit > div i {
    padding: 5px;
  }

  .ac-esign-tool-wrapper_edit > div span.active {
    color: var(--success);
  }
  .ac-esign-tool-wrapper_edit > div .fe-edit-3 {
    color: var(--black);
  }

  .ac-esign-tool-wrapper .resizable-rb {
    background: black;
    right: -10px !important;
    bottom: -10px !important;
  }

  .ac-esign-tool-wrapper .resizable-r,
  .ac-esign-tool-wrapper .resizable-b,
  .ac-esign-tool-wrapper .resizable-l,
  .ac-esign-tool-wrapper .resizable-lb,
  .ac-esign-tool-wrapper .resizable-t,
  .ac-esign-tool-wrapper .resizable-lt,
  .ac-esign-tool-wrapper .resizable-rt {
    display: none !important;
  }

  .ac-esign-tool-wrapper .hide_handlers .resizable-rb {
    display: none !important;
  }
  .resizable-component > .resizable-rb[data-v-7a68f144] {
    z-index: -1;
  }
</style>
